import React, { Component } from 'react'
import {Button, Modal} from 'antd'
import {connect} from 'dva'
import {saveRef} from '@/utils/utils'

import BaseContent from '@/components/BaseContent'
import BaseTable from '@/components/BaseTable'

import styles from './style.module.less'

const TABLE_KEY = 'questions_type_id';

const mapState = store => {
  return {...store.questionsType}
};


class AddModal extends Component {

  constructor(props) {
    super(props);

    this.type = {
      CREATE: Symbol('create'),
      MODIFY: Symbol('modify'),
    };

    this.state = {
      type: this.type.CREATE,
      visible: false
    };
  }

  create () {
    this.setState({
      type: this.type.CREATE,
      visible: true
    });
  }

  edit () {
    this.setState({
      type: this.type.MODIFY,
      visible: true
    });
  }

  handleOk = () => {
    console.log('确定')
    this.setState({
      visible: false
    });
  }

  handleCancel = () => {
    console.log('取消')
    this.setState({
      visible: false
    });
  }

  render () {
    return (
      <Modal
        visible={this.state.visible}
        width={600}
        footer={[
          <Button size="large" key="save" type="primary" onClick={this.handleOk}>确定</Button>,
          <Button size="large" key="cancel" onClick={this.handleCancel}>取消</Button>,
        ]}
        bodyStyle={{
            padding: '24px 80px',
        }}
      >
        <div className={styles.addModal}>
          <h2 className={styles.title}>
            {
              this.state.type === this.type.CREATE ? '创建新类型' : '修改类型'
            }
          </h2>
          <div>
            <input type="text" className={styles.input} placeholder="请输入类型名称" />
          </div>
        </div>
      </Modal>
    )
  }
}

@connect(mapState)
class QuestionsType extends Component {
  componentDidMount () {
    this.props.dispatch({type: 'questionsType/fetch'})
  }

  handleCreate = () => {
    this.addModal.create()
  }

  getColumns () {
    const columns = [{
      title: '类型ID',
      dataIndex: 'questions_type_id',
      key: 'questions_type_id',
    }, {
      title: '类型名称',
      dataIndex: 'questions_type_text',
      key: 'questions_type_text',
    }, {
      title: '操作',
      key: 'action',
    }];
    return columns
  }

  render() {
    const {table} = this.props

    return (
      <BaseContent>
        <React.Fragment>
          <BaseTable
            columns={this.getColumns()}
            buttons={
              [
                <Button
                  type="primary"
                  icon="plus" 
                  size="large"
                  onClick={this.handleCreate}
                >添加类型</Button>
              ]
            }
            rowKey={TABLE_KEY}
            dataSource={table.data}
            wrapStyle={{
                padding: 0,
            }}
          />
        </React.Fragment>
        <AddModal ref={saveRef(this, 'addModal')}/>
      </BaseContent>
    )
  }
}

export default QuestionsType
