import React from 'react';
import classNames from 'classnames';
import { Table } from 'antd';
// import { generateIndexKey } from 'common/convenience';

import styles from './style.module.less';

const BASE_PAGINATION = {
    defaultCurrent: 1,
    defaultPageSize: 10,
    showQuickJumper: true,
    showSizeChanger: true,
    pageSizeOptions: ['5', '10', '20', '50', '100']
};

/**
 * 基础表格
 * @prop {Object|boolean} pagination - 分页对象
 * @prop {string} wrapClassName - 外层类名
 * @prop {Object} wrapStyle - 外层样式
 * @prop {string} className - 表格类名
 * @prop {Object} style - 表格样式
 * @prop {Array} buttons - 操作按钮
 * @prop {boolean} [bordered=true] - 表格是否带边框
 */

const BaseTable = ({
    pagination, wrapClassName, wrapStyle, className, style, buttons, bordered = false, ...rest
}) => {
    const allPagination = pagination ? { ...BASE_PAGINATION, ...pagination } : false;
    return (
        <div
            style={wrapStyle}
            className={classNames(styles.container, wrapClassName)}
        >
            {buttons && (
                <div className={styles.buttons}>
                    {buttons.map((one, index) => {
                        return React.cloneElement(
                            one,
                            { key: index },
                        );
                    })}
                </div>
            )}
            <Table
                {...rest}
                bordered={bordered}
                pagination={allPagination}
                style={style}
                className={classNames(styles.table, className)}
            />
        </div>
    );
};

export default BaseTable;
